}
body{
    margin:0 auto;
    width:100%;

}
header {
  height: 8em;
  display: flex;
  align-items: center;
  border-bottom: 1px solid;
  justify-content: left;
  background: #6441a5; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    #bd98db,
    #6441a5
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(
    left,
    #b62424,
    rgb(229, 160, 160)
  );
  background: linear-gradient(
    to right,
    #b62424,
    rgb(229, 160, 160)
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

header > img {
  height: 75%;
  margin: 1em 0em 1em 2em;
}

header > p {
  font-family: VT323, monospace;
  color: whitesmoke;
  font-size: 2rem;
  margin-left: 1.5rem;
}

.meme {
  position: relative;
  width: 100%;
  margin: auto;
  text-align: center;
}

.meme > img {
  width: 100%;
  max-width: 55em;
  height: auto;
  margin-bottom: 1.5em;
  background: cover;
  background-repeat: no-repeat;
}

.meme > h2 {
  position: absolute;
  max-width: 80%;
  text-align: center;
  left: 49%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 0px;
  font-family: impact, sans-serif;
  text-transform: uppercase;
  color: white;
  letter-spacing: 2px;
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000,
    -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000,
    2px 2px 5px #000;
}

.meme > .bottom {
  bottom: 10px;
}

.meme > .top {
  top: 10px;
}

.meme-form {
  width: 90%;
  margin: 20px auto;
  display: inline-block;
  justify-content: space-between;
}

.meme-form > input {
  border: 2px solid;
  border-radius: 5px;
  margin-left: 65px;
  display: block;
  width: 50%;
  height: 40px;
}

.meme-form > button {
  height: 50px;
  width: 170px;
  margin-top: 10px;
  margin-left: 65px;
  border: 3px rgb(221, 210, 210) solid;
  font-family: VT323, monospace;
  font-size: 25px;
  letter-spacing: 1.5px;
  color: white;
  background: rgb(35, 86, 134);
}

.meme-form > input::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: left;
}
.meme-form > input::-moz-placeholder {
  /* Firefox 19+ */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: left;
}
.meme-form > input:-ms-input-placeholder {
  /* IE 10+ */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: left;
}
.meme-form > input:-moz-placeholder {
  /* Firefox 18- */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: left;
}

