.meme {
  position: relative;
  width: 100%;
  margin: auto;
  text-align: center;
}

.meme > img {
  width: 100%;
  max-width: 55em;
  height: auto;
  margin-bottom: 1.5em;
  background: cover;
  background-repeat: no-repeat;
}

.meme > h2 {
  position: absolute;
  max-width: 80%;
  text-align: center;
  left: 49%;
  transform: translateX(-50%);
  margin: 15px 0;
  padding: 0 0px;
  font-family: impact, sans-serif;
  text-transform: uppercase;
  color: white;
  letter-spacing: 2px;
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000,
    -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000,
    2px 2px 5px #000;
}

.meme > .bottom {
  bottom: 10px;
}

.meme > .top {
  top: 10px;
}

.meme-form {
  width: 90%;
  margin: 20px auto;
  display: inline-block;
  justify-content: space-between;
}

.meme-form > input {
  border: 2px solid;
  border-radius: 5px;
  margin-left: 65px;
  display: block;
  width: 50%;
  height: 40px;
}

.meme-form > button {
  height: 50px;
  width: 170px;
  margin-top: 10px;
  margin-left: 65px;
  border: 3px rgb(221, 210, 210) solid;
  font-family: VT323, monospace;
  font-size: 25px;
  letter-spacing: 1.5px;
  color: white;
  background: rgb(35, 86, 134);
}

.meme-form > input::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: left;
}
.meme-form > input::-moz-placeholder {
  /* Firefox 19+ */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: left;
}
.meme-form > input:-ms-input-placeholder {
  /* IE 10+ */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: left;
}
.meme-form > input:-moz-placeholder {
  /* Firefox 18- */
  font-family: VT323, monospace;
  font-size: 25px;
  text-align: left;
}
